<template>
  <div class="info">
    <!--  -->
    
    <!--  -->
    <div :key="index" v-for="(group,index) in groupList">
      <div @click="handleClick(group.ACGRP)" class="titlelist">{{group.ACGRN}}     >
        <!--  -->
      </div>
      <!-- content -->
      
      <transition name="slide-fade">
        <div v-if="activeName==group.ACGRP" class="info-content">
          <!-- list title -->
          <div class="info-title">
            <span class="info-title-left" @click="closeInfo()"></span>
            <span>{{group.ACGRN}}</span>
            <span class="info-title-right" @click="allSubmit()"></span>
          </div>
          <!-- list content -->
          <div class="info-content-b" v-for="(sonGroup,sonIndex) in group.ACBLK " :key="sonIndex">
            <div class="main-content-box mt20" v-for="(items,itemsIndex) in sonGroup.INFTY " :key="itemsIndex" v-if="items.ZZHID != 'X'">
                <div class="main-content-title">{{items.INFTX}}</div>
                <div v-for="(item,index) in items.FIELD" v-if="items.ZZTAB != 'X' && item.ZZHID !='X'" :key="index">
                  <!-- date -->
                  <div v-if="item.FTYPE == 'D'" >
                    <div class="main-field">
                      <span v-if="item.ZZREQ=='X'" style="color:red">*</span>
                      {{item.FIELN}}</div>
                    <cube-input v-model="item.value" @click.native.prevent="showDatePicker(item.FIELN,item.value,item)"></cube-input>
                  </div>
                  <!-- input -->
                  <div v-if="item.ZZSHM != 'D'  && item.ZZSHM != 'T' && item.FTYPE != 'D' && item.ZKJLX != 'TXT' && item.ZKJLX != 'BUT' && item.ZKJLX != 'STA'  && item.ZKJLX != 'MON'&&item.ZZSHM != 'B' && item.ZZSHM != 'Z' && item.ZZSHM != 'S' && item.ZKJLX != 'PSR'" >
                    <div class="main-field">
                      <span v-if="item.ZZREQ=='X'" style="color:red">*</span>
                      {{item.FIELN}}</div>
                    <cube-input 
                      v-model="item.value" 
                      @input="item.ZZDIS !='X' ? dataValidation(item) : ''"
                      ></cube-input>
                    <div style="color:red;font-size:12px;" v-if="item.isError">{{item.FIELN}}</div>
                  </div>
                  <!-- select -->
                  <div v-if="item.ZZSHM == 'D'" >
                    <div class="main-field">
                      <span v-if="item.ZZREQ=='X'" style="color:red">*</span>
                      {{item.FIELN}}
                    </div>
                   
                    <cube-select 
                      :title="item.FIELN"
                      v-model="item.value"
                      :options="item.valueList" 
                      @click.native="item.ZZDIS !='X' ? searchPe(item.FIELD,items.INFTY,items.SUBTY,group.ACGRP,sonGroup.ACBLK,item ,items) : ''"
                      v-bind:disabled="item.ZZDIS =='X'"
                      @change="change(item.EVEN,item.value,item.FIELD,items.INFTY,items.SUBTY,group.ACGRP,sonGroup.ACBLK,item)"
                     >
                    </cube-select>
                  </div>

                </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
    
    
    
    <!--  -->
    <cube-button :primary="true"></cube-button>
  </div>
</template>

<script>
import _DATA from './data.json';
import _SELECTDATA from './select.json';
  export default {
    name: "personnelEventEdit",
    
    data() {
      return {
        _initData:'',

        tags:[],
        loading:false,
        currentUser: JSON.parse(sessionStorage.getItem('user')),
        servicePath:"/personnelService/pa40/",
        groupList: [],
        zacti: "",
        MENUII: "",
        SUBKEY: "",
        fileName:"",
        fileList: [],
        fileListDefinition: [],
        GUID: "",
        STAT: "",
        isFile:false,
        employeeNum: "",
        activeName: "",
        treeDataCreate: [],
        isCloce: false,
        isPostion: true,
        isShowTree: false,
        
        sitem: {},
        sitems: "",
        sgroup: "",
        ssonGroup: "",
        info: "",
        func: "",
        rules: {},
        buttonList:"",
        headhtmlData:"",
        photo:"",
        sendMessageData:{},
        dialogVisible:false,
        dialogTitle:"",
        isSearch:false,
        ZTYPE:"",
        errCount:0,

        fieldsName:'',//
      }
    },
    mounted() {
      let _this = this;
      document.addEventListener('click', function (e) {
        if (e.target.id !== 'targetTreeInp') {
          //_this.isShowTree = false;
          //_this.isShowTree = false;
          _this.sitem != undefined ? _this.sitem.isShow = false  : _this.sitem = {};
        }
      })
    },
    methods: {
      showDatePicker(title,val,item) {
        // title
        this.fieldsName=item;
        this.datePicker = this.$createDatePicker({
          title: title,
          min: new Date(2008, 7, 8),
          max: new Date(2020, 9, 20),
          value: val? new Date(val) : new Date(),
          onSelect: this.selectHandle
        })
        this.datePicker.show()
      },
      selectHandle(date, selectedVal, selectedText) {
        let t_this=this;
        t_this.$set(t_this.fieldsName,'value',selectedVal.join('-'));
        this.fieldsName='';
      },
      format(date, fmt) { //author: meizz
        var o = {
          "M+": date.getMonth() + 1, //
          "d+": date.getDate(), //
          "h+": date.getHours(), //
          "m+": date.getMinutes(), //
          "s+": date.getSeconds(), //
          "q+": Math.floor((date.getMonth() + 3) / 3), //
          "S": date.getMilliseconds() //
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
          if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
      },
      sortInfo(list, key, type) {
        list.sort(function (a, b) {
          if (type == "asc") {
            return a[key] - b[key];
          } else {
            return b[key] - a[key];
          }
        })
        return list;
      },
      cloneObj(obj) {
        var str, newobj = obj.constructor === Array ? [] : {};
        if(typeof obj !== 'object'){
          return;
        } else if(window.JSON){
          str = JSON.stringify(obj), //
            newobj = JSON.parse(str); //
        } else {
          for(var i in obj){
            newobj[i] = typeof obj[i] === 'object' ? cloneObj(obj[i]) : obj[i];
          }
        }
        return newobj;
      },
      handleClick(res) {//
        this.activeName=res;
      },
      closeInfo(){
        this.activeName='';
      },
      getTreeNode(chooseObj, item) {
        item.value = chooseObj.id + "-" + chooseObj.value;
      },
      getPernrData(data,item) {
        item.value = {
          KEY: data.employeeNum,
          VALUE: data.employeeName + "(" + data.employeeNum + ")"
        };
      },
      /**
       * 
       */
      initFileds(employeeNum) {
        let t_this = this;
        var params = {
          zacti: "",
          param: JSON.stringify({
            MENUII: t_this.MENUII,
            SUBKEY: t_this.SUBKEY
          }),
          pernr: employeeNum,
          userId:"",
          ztype:t_this.ZTYPE
        };
        //http("get", url, params, {}, function (response) {
        // httpRequest4Post(t_this, t_this.servicePath + "getFields", params, function (response) {

          t_this.groupList = [];
          var data = this._initData;
          t_this.groupList = [];
          var files = []; //
          var group = [];  //
          var inf = [];    //
          t_this.zacti = "";
          if (data != "") {
            for (var i in data) {
              if (data[i].pname == "ET_FILE") {  //
                files = JSON.parse(data[i].value);
              } else if (data[i].pname == "ET_GRP") { //
                group = JSON.parse(data[i].value);
              } else if (data[i].pname == "ET_INF") {
                inf = JSON.parse(data[i].value);   //
              } else if (data[i].pname == "EV_ZACTI") {
                t_this.zacti = JSON.parse(data[i].value);
              } else if (data[i].pname == "ES_MESSAGE") { //
                var message = JSON.parse(data[i].value);
                if (message.MSGTY == "E") {  //
                  t_this.$alert(message.MSGTX, '', {type: 'error', center: true});
                  return;
                }
              }
            }
            //
            group = t_this.sortInfo(group, "SORTD", "asc")
            for (var i in group) {
              for (var j in group[i].ACBLK) {
                var sonGroup = group[i].ACBLK[j].INFTY;
                for (var k in sonGroup) {
                  if (sonGroup[k].ZZTAB == "X") {  // && sonGroup[k].ACTIO != "DIS"
                    var valueList = sonGroup[k].VALUE != "" && sonGroup[k].VALUE != undefined ? JSON.parse(sonGroup[k].VALUE) : [];
                    sonGroup[k].models = [];
                    if (valueList.length > 0) {   //value 
                      for (var n in valueList) {
                        sonGroup[k].models.push(t_this.cloneObj(sonGroup[k].FIELD));
                        //sonGroup[k].models.push(sonGroup[k].FIELD);
                      }
                    } else {
                      sonGroup[k].models.push(t_this.cloneObj(sonGroup[k].FIELD));
                      //sonGroup[k].models.push(sonGroup[k].FIELD);
                    }
                  }
                }
              }
            }

            //
            for (var i in group) {
              group[i].ACBLK = t_this.sortInfo(group[i].ACBLK, "SORTD", "asc")
              for (var j in group[i].ACBLK) {
                var sonGroup = t_this.sortInfo(group[i].ACBLK[j].INFTY, "SORTD", "asc");
                t_this.sonGroup = sonGroup;
                for (var k in sonGroup) {
                  if (sonGroup[k].ZZTAB == "X" && sonGroup[k].ACTIO != "DIS" && sonGroup[k].ACTIO != "MOD") {  //
                    var models = t_this.sortInfo(sonGroup[k].models, "SORTD", "asc");
                    for (var n in models) {
                      var fields = t_this.sortInfo(models[n], "SORTD", "asc");
                      for (var o  in fields) {
                        //item.FIELD+items.INFTY+items.SUBTY+group.ACGRP+sonGroup.ACBLK+$index
                        if (fields[o].ZZREQ == "X") {
                          let rule = [{required: true}]
                          t_this.rules[fields[o].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                        }
                        if (fields[o].FTYPE == 'D') {   //
                          fields[o].value = fields[o].ZZDEF != "" && fields[o].ZZDEF != "00000000" ? t_this.format(new Date(fields[o].ZZDEF.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")), "yyyy-MM-dd") : "";
                        } else if (fields[o].ZZSHM == 'D') { //
                          if (fields[o].ZZDEF != "") {
                            fields[o].value = JSON.parse(fields[o].ZZDEF);
                            fields[o].valueList = [];
                            fields[o].valueList.push(fields[o].value);
                          } else {
                            fields[o].value = "";
                            fields[o].valueList = [];
                          }
                        } else if (fields[o].ZZSHM == 'T') { //
                          fields[o].isShow = false;
                          fields[o].treeDataCreate = "";
                          if (fields[o].ZZDEF != "") {
                            var value = JSON.parse(fields[o].ZZDEF);
                            fields[o].value = value.KEY + "-" + value.VALUE;
                          } else {
                            fields[o].value = "";
                          }

                        } else {
                          if (fields[o].ZZDEF != "") {
                            fields[o].value = fields[o].ZZDEF;
                            if (fields[o].ZZBDS != "") {
                              fields[o].ZZBDS = new RegExp(fields[o].ZZBDS.replace(/\s/g, "")); //
                            } else {
                              fields[o].ZZBDS = "";
                            }
                          } else {
                            fields[o].value = "";
                            if (fields[o].ZZBDS != "") {
                              fields[o].ZZBDS = new RegExp(fields[o].ZZBDS.replace(/\s/g, "")); //
                            } else {
                              fields[o].ZZBDS = "";
                            }
                          }

                        }
                      }
                    }
                  } else if (sonGroup[k].ZZTAB == "X" && (sonGroup[k].ACTIO == "DIS" || sonGroup[k].ACTIO == "MOD")) {  //  
                    var valueList = sonGroup[k].VALUE != "" ? JSON.parse(sonGroup[k].VALUE) : "";
                    var models = sonGroup[k].models;
                    for (var o  in valueList) {
                      var values = valueList[o];
                      for (var l in models[o]) {
                        if (models[o][l].ZZREQ == "X") {
                          let rule = [{required: true}]
                          t_this.rules[models[o][l].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                        }
                        models[o][l].value = values[models[o][l].FIELD];
                        if (models[o][l].FTYPE == 'D') {   //
                          models[o][l].value = models[o][l].value != "" && models[o][l].value != "00000000" ? t_this.format(new Date(models[o][l].value.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")), "yyyy-MM-dd") : "";
                        } else if (models[o][l].ZZSHM == 'D') { //
                          if (models[o][l].value != "") {
                            models[o][l].value = JSON.parse(values[models[o][l].FIELD]);
                            models[o][l].valueList = [];
                            models[o][l].valueList.push(models[o][l].value);
                          } else {
                            models[o][l].value = "";
                            models[o][l].valueList = [];
                          }
                        } else if (models[o][l].ZZSHM == 'T') { //
                          models[o][l].isShow = false;
                          models[o][l].treeDataCreate = "";
                          if (models[o][l].value != "") {
                            var value = JSON.parse(values[models[o][l].FIELD]);
                            models[o][l].value = value.KEY != "" ? value.KEY + "-" + value.VALUE : value.VALUE;
                          } else {
                            models[o][l].value = "";
                          }

                        } else {
                          if (models[o][l].value != "") {
                            models[o][l].value = values[models[o][l].FIELD];
                            if (models[o][l].ZZBDS != "") {
                              models[o][l].ZZBDS = new RegExp(models[o][l].ZZBDS.replace(/\s/g, "")); //
                            } else {
                              models[o][l].ZZBDS = "";
                            }
                          } else {
                            models[o][l].value = "";
                            if (models[o][l].ZZBDS != "") {
                              models[o][l].ZZBDS = new RegExp(models[o][l].ZZBDS.replace(/\s/g, "")); //
                            } else {
                              models[o][l].ZZBDS = "";
                            }
                          }

                        }
                      }
                    }

                  } else {
                    var fields = t_this.sortInfo(sonGroup[k].FIELD, "SORTD", "asc");// this.cloneObj(obj.FIELD);sonGroup[k].FIELD;
                    for (var n in fields) {
                      if (fields[n].ZZREQ == "X") {
                        let rule = [{required: true}]
                        t_this.rules[fields[n].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                      }
                      if (fields[n].FTYPE == 'D') {   //
                        fields[n].value = fields[n].ZZDEF != "" && fields[n].ZZDEF != "00000000" ? t_this.format(new Date(fields[n].ZZDEF.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")), "yyyy-MM-dd") : "";
                      } else if (fields[n].ZZSHM == 'D') { //
                        if (fields[n].ZZDEF != "") {
                          // debugger
                          var _data={
                            value:JSON.parse(fields[n].ZZDEF).KEY,
                            text:JSON.parse(fields[n].ZZDEF).VALUE
                          }
                          fields[n].value =_data;
                          fields[n].valueList = [];
                          fields[n].valueList.push(_data);
                        } else {
                          fields[n].value = "";
                          fields[n].valueList = [];
                        }
                      } else if (fields[n].ZZSHM == 'T') { //
                        fields[n].isShow = false;
                        fields[n].treeDataCreate = "";
                        if (fields[n].ZZDEF != "") {
                          var value = JSON.parse(fields[n].ZZDEF);
                          fields[n].value = value.KEY + "-" + value.VALUE;
                        } else {
                          fields[n].value = "";
                        }

                      } else {
                        if (fields[n].ZZDEF != "") {
                          fields[n].value = fields[n].ZZDEF;
                          if (fields[n].ZZBDS != "") {
                            fields[n].ZZBDS = new RegExp((fields[n].ZZBDS).replace(/\s/g, "")); //
                          } else {
                            fields[n].ZZBDS = "";
                          }
                          fields[n].ZZBDS = ""; //
                        } else {
                          fields[n].value = "";
                          if (fields[n].ZZBDS != "") {
                            fields[n].ZZBDS = new RegExp((fields[n].ZZBDS).replace(/\s/g, "")); //
                          } else {
                            fields[n].ZZBDS = "";
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          t_this.groupList = group;
          /**
           *  
           * @type {Array}
           */
          t_this.fileList = []; //
          t_this.fileListDefinition = []; // 
          for (var i in files) {
            if (files[i].ZZREQ == "X") {
              t_this.fileList.push(files[i]);
            } else {
              t_this.fileListDefinition.push(files[i])
            }
          }
        // });
      },
      /**
       * 
       * 
       */
      getAcData(even) {
        let t_this = this;
        var params = {
          guid: t_this.GUID,
          even: "",
          userId:"",
          //bip: t_this.STAT == 'K' || t_this.STAT == 'Q' ? "X" : "",
          ipage: t_this.MENUII,
          ztype:t_this.ZTYPE
        }
        //http("get", url, params, {}, function (response) {
        httpRequest4Post(t_this, t_this.servicePath + "getAcData", params, function (response) {
          t_this.groupList =[];
          var data = response.data.result;
          var files = []; //
          var group = [];  //
          var fileData = [];
          t_this.rules = {};
          t_this.zacti = "";
          if (data != "") {
            for (i in data) {
              if (data[i].pname == "ET_GRP") {
                group = JSON.parse(data[i].value)  //
              } else if (data[i].pname == "EV_DATA") {
                fileData = JSON.parse(data[i].value)  //VALUE

              } else if (data[i].pname == "EV_ZACTI") {
                t_this.zacti = JSON.parse(data[i].value)   //
              } else if (data[i].pname == "EV_PERNR") {
                if (JSON.parse(data[i].value) != "") {
                  t_this.employeeNum = JSON.parse(data[i].value);
                  t_this.getHeadInfo(JSON.parse(data[i].value));
                }

              } else if (data[i].pname == "ET_FILE") {
                files = JSON.parse(data[i].value);
              } else if (data[i].pname == "ES_MESSAGE") {
                var message = JSON.parse(data[i].value);
                if (message.MSGTY == "E") {  //
                  t_this.$message.error(message.MSGTX);
                  return;
                }
              }
            }

            //
            for (var i in group) {
              for (var j in group[i].ACBLK) {
                var sonGroup = group[i].ACBLK[j].INFTY;
                for (var k in sonGroup) {
                  for (var s in fileData) {
                    if (sonGroup[k].INFTY == fileData[s].INFTY && sonGroup[k].SUBTY == fileData[s].SUBTY) {
                      var valueList = JSON.parse(fileData[s].VALUE);
                      sonGroup[k].models = [];
                      if (valueList.length > 1) {   //value 
                        for (var n in valueList) {
                          sonGroup[k].models.push(t_this.cloneObj(sonGroup[k].FIELD));
                        }
                      } else {
                        sonGroup[k].models.push(t_this.cloneObj(sonGroup[k].FIELD));
                      }
                    }
                  }

                }
              }
            }

            if (fileData == "") {
              return;
            }
            //
            for (var i in group) {
              group[i].ACBLK = t_this.sortInfo(group[i].ACBLK, "SORTD", "asc");
              for (var j in group[i].ACBLK) {
                var sonGroup = t_this.sortInfo(group[i].ACBLK[j].INFTY, "SORTD", "asc");
                for (var k in sonGroup) {
                  for (var t in fileData) {
                    if (fileData[t].INFTY == sonGroup[k].INFTY && fileData[t].SUBTY == sonGroup[k].SUBTY) { //value
                      var valueList = JSON.parse(fileData[t].VALUE);
                      if (sonGroup[k].ZZTAB == "X") {
                        var models = sonGroup[k].models;
                        for (var o  in valueList) {
                          var values = valueList[o];
                          for (var l in models[o]) {
                            models[o][l].value = values[models[o][l].FIELD] == undefined ? "" : values[models[o][l].FIELD];
                            if (models[o][l].ZZREQ == "X") {
                              let rule = [{required: true}]
                              t_this.rules[models[o][l].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                            }
                            if (models[o][l].FTYPE == 'D') {   //
                              models[o][l].value = models[o][l].value != "" && models[o][l].value != "00000000" ?  t_this.$DateUtils.format(new Date(models[o][l].value.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")),"yyyy-MM-dd") : "";
                            } else if (models[o][l].ZZSHM == 'D') { //
                              if (models[o][l].value != "") {
                                models[o][l].value = JSON.parse(values[models[o][l].FIELD]);
                                models[o][l].valueList = [];
                                models[o][l].valueList.push(models[o][l].value);
                              } else {
                                if (models[o][l].ZZDEF != "") {
                                  models[o][l].value = JSON.parse(models[o][l].ZZDEF);
                                  models[o][l].valueList = [];
                                  models[o][l].valueList.push(JSON.parse(models[o][l].ZZDEF));
                                } else {
                                  models[o][l].value = "";
                                  models[o][l].valueList = [];
                                }

                              }
                            } else if (models[o][l].ZZSHM == 'T') { //
                              models[o][l].isShow = false;
                              models[o][l].treeDataCreate = "";
                              if (models[o][l].value != "") {
                                var value = JSON.parse(values[models[o][l].FIELD]);
                                models[o][l].value = value.KEY != "" ? value.KEY + "-" + value.VALUE : "";
                              } else {
                                if (models[o][l].ZZDEF != "") {
                                  var value = JSON.parse(models[o][l].ZZDEF);
                                  models[o][l].value = value.KEY != "" ? value.KEY + "-" + value.VALUE : "";
                                } else {
                                  models[o][l].value = "";
                                }
                              }

                            } else {
                              if (models[o][l].value != "") {
                                models[o][l].value = t_this.replaceAllSpace(values[models[o][l].FIELD]);
                                models[o][l].ZZBDS = new RegExp((models[o][l].ZZBDS != "" ? models[o][l].ZZBDS.source : models[o][l].ZZBDS).replace(/\s/g, "")); //
                              } else {
                                if (models[o][l].ZZDEF != "") {
                                  models[o][l].value = models[o][l].ZZDEF;
                                  models[o][l].ZZBDS = new RegExp((models[o][l].ZZBDS != "" ? models[o][l].ZZBDS.source : models[o][l].ZZBDS).replace(/\s/g, "")); //
                                } else {
                                  models[o][l].value = "";
                                  models[o][l].ZZBDS = new RegExp((models[o][l].ZZBDS != "" ? models[o][l].ZZBDS.source : models[o][l].ZZBDS).replace(/\s/g, "")); //
                                }
                              }

                            }
                          }
                        }
                      }
                      else {
                        var values = valueList[0];
                        var fields = t_this.sortInfo(sonGroup[k].FIELD, "SORTD", "asc");
                        for (var o in  fields) {
                          fields[o].value = values != undefined ? (values[fields[o].FIELD] == undefined ? "" : values[fields[o].FIELD]) : "";
                          if (fields[o].ZZREQ == "X") {
                            let rule = [{required: true}]
                            t_this.rules[fields[o].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                          }
                          if (fields[o].FTYPE == 'D') {   //
                            fields[o].value = fields[o].value != "" && fields[o].value != "00000000" ?  t_this.$DateUtils.format(new Date(fields[o].value.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")),"yyyy-MM-dd") : "";
                          } else if (fields[o].ZZSHM == 'D') { //
                            if (fields[o].value != "") {
                              fields[o].value = JSON.parse(values[fields[o].FIELD]);
                              fields[o].valueList = [];
                              fields[o].valueList.push(fields[o].value);
                            } else {
                              fields[o].value = "";
                              fields[o].valueList = [];
                              if (fields[o].ZZDEF != "") {
                                fields[o].value = JSON.parse(fields[o].ZZDEF);
                                fields[o].valueList = [];
                                fields[o].valueList.push(JSON.parse(fields[o].ZZDEF));
                              } else {
                                fields[o].value = "";
                                fields[o].valueList = [];
                              }
                            }
                          } else if (fields[o].ZZSHM == 'T') { //
                            fields[o].isShow = false;
                            fields[o].treeDataCreate = "";
                            if (fields[o].value != "") {
                              var value = JSON.parse(values[fields[o].FIELD]);
                              fields[o].value = value.KEY != "" ? value.KEY + "-" + value.VALUE : "";
                            } else {
                              if (fields[o].ZZDEF != "") {
                                var value = JSON.parse(fields[o].ZZDEF);
                                fields[o].value = value.KEY != "" ? value.KEY + "-" + value.VALUE : "";
                              } else {
                                fields[o].value = "";
                              }
                            }

                          } else if (fields[o].ZZSHM == 'S') {
                            if (fields[o].value != "") {
                              fields[o].value = JSON.parse(values[fields[o].FIELD]);
                            } else {
                              fields[o].value = "";
                              if (fields[o].ZZDEF != "") {
                                fields[o].value = JSON.parse(fields[o].ZZDEF);
                              } else {
                                fields[o].value = "";
                              }
                            }
                          } else if (fields[o].ZZSHM == 'Z') {  //
                            if (fields[o].value != "") {
                              fields[o].value = JSON.parse(values[fields[o].FIELD]);
                            } else {
                              fields[o].value = "";
                              if (fields[o].ZZDEF != "") {
                                fields[o].value = JSON.parse(fields[o].ZZDEF);
                              } else {
                                fields[o].value = "";
                              }
                            }
                          } else if (fields[o].ZZSHM == 'B') {  //
                            if (fields[o].value != "") {
                              fields[o].value = JSON.parse(values[fields[o].FIELD]);
                            } else {
                              fields[o].value = "";
                              if (fields[o].ZZDEF != "") {
                                fields[o].value = JSON.parse(fields[o].ZZDEF);
                              } else {
                                fields[o].value = "";
                              }
                            }
                          }else if(fields[o].ZKJLX == "PSR"){//
                            if (fields[o].value != "") {
                              fields[o].value = JSON.parse(values[fields[o].FIELD]);
                            } else {
                              fields[o].value = "";
                              if (fields[o].ZZDEF != "") {
                                fields[o].value = JSON.parse(fields[o].ZZDEF);
                              } else {
                                fields[o].value = "";
                              }
                            }
                          } else {
                            if (fields[o].value != "") {
                              fields[o].value = t_this.replaceAllSpace(values[fields[o].FIELD]);
                              fields[o].ZZBDS = new RegExp((fields[o].ZZBDS).replace(/\s/g, "")); //
                            } else {

                              if (fields[o].ZZDEF != "") {
                                fields[o].value = fields[o].ZZDEF;
                                fields[o].ZZBDS = new RegExp((fields[o].ZZBDS).replace(/\s/g, "")); //
                              } else {
                                fields[o].value = "";
                                fields[o].ZZBDS = new RegExp((fields[o].ZZBDS).replace(/\s/g, "")); //
                              }
                            }

                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          t_this.groupList = group;
          if (t_this.groupList.length > 0 && (t_this.activeName == "" || t_this.activeName == "0")) {
            let count = 0;
            for(var y in t_this.groupList){
              if(t_this.groupList[y].ZZHID == "X"){
                count ++;
              }
            }
            if(count == t_this.groupList.length){
              t_this.activeName = "fileTabPane";
            }else{
              //sheet
              t_this.activeName = t_this.groupList[0].ACGRP;
            }

          }
          /**
           *  
           * @type {Array}
           */
          t_this.fileList = []; //
          t_this.fileListDefinition = []; // 
          for (i in files) {
            if (files[i].ZZREQ == "X") {
              t_this.fileList.push(files[i]);
            } else {
              t_this.fileListDefinition.push(files[i])
            }
          }
        })
