      init(employeeNum){
        debugger
        let _this = this;
        //
        _this.info = this.$route.params.info;
        //_this.MENUII = sessionStorage.pageCode;
        if (_this.info) {
          let paramData =JSON.parse(_this.info);
          _this.MENUII = paramData.MENUII != undefined ? paramData.MENUII : "";//
          _this.SUBKEY = paramData.SUBKEY;//
          _this.func = paramData.FUNC;//
          _this.GUID = paramData.GUID != undefined  ? paramData.GUID : "";//guid
          _this.STAT = paramData.ZSTAT != undefined ? paramData.ZSTAT : "";//
          _this.ZTYPE = paramData.ZYWTYPE != undefined ? paramData.ZYWTYPE : "";//
        }
        if(_this.GUID != undefined && _this.GUID != "" && _this.employeeNum == ""){
          _this.getAcData();//
        }else if(employeeNum != undefined && employeeNum != ""){
          _this.initFileds(employeeNum)//
        }else{
          //
          if(_this.MENUII == "PAZZGL" || _this.MENUII == "WYLZ" || _this.MENUII == "PADISP"){
            _this.employeeNum = _this.currentUser.PERNR;
            var param = {
              pernr:_this.employeeNum,
              ipage:_this.MENUII
            }
            httpRequest4Post(_this, _this.servicePath+"getGuid", param, function (response) {
              let data = response.data.result
              let length = data.length;
              for (let i = 0; i < length; i++) {
                if ('EV_GUID' === data[i].pname) {
                  _this.GUID = JSON.parse(data[i].value);
                  if(_this.GUID != "" && _this.GUID != "00000000000000000000"){
                    _this.getAcData();
                  }else{
                    _this.getHeadInfo(_this.employeeNum);//
                    _this.initFileds(_this.employeeNum);//
                  }
                }
              }
            }, function (response) {
            });
          }else if(_this.MENUII == "PARZGL" || (_this.MENUII == "PALZGL" && _this.SUBKEY == "90") ){//
            _this.isSearch = false;
            _this.initFileds("");//
          }else{
            _this.isSearch = true;
          }
        }
