      /**
       * value
       * @param fields  value
       * @param items 
       */
      emptyField(fields, items) {
        if (fields.length > 0) {
          if (items.FIELD == undefined) {
            for (var i in items) {
              for (var j in fields)
                if (items[i].FIELD == fields[j].FIELD) {
                  //value = ""
                  if (items[i].ZZSHM == 'T') {
                    items[i].value = "";
                    items[i].orgList = [];
                  } else {
                    items[i].value = {KEY: "", VALUE: ""};
                    items[i].valueList = [];
                  }

                }
            }
          } else {
            for (var i in items.FIELD) {
              for (var j in fields)
                if (items.FIELD[i].FIELD == fields[j].FIELD) {
                  //value = ""
                  if (items.FIELD[i].ZZSHM == 'T') {
                    items.FIELD[i].value = "";
                    items.FIELD[i].orgList = [];
                  } else {
                    items.FIELD[i].value = {KEY: "", VALUE: ""};
                    items.FIELD[i].valueList = [];
                  }

                }
            }
          }
        }
      },
      getDataSearch(obj) {
        let t_this = this;
        var iv_data = [];
        var obj2 = {};
        var fields = [];
        if (obj instanceof Array) {
          fields = obj;
        } else {
          fields = obj.FIELD;
        }

        for (var n in fields) {
          if (fields[n].FTYPE == 'D') {   //
            obj2[fields[n].FIELD] = fields[n].value != "" && fields[n].value != undefined ? t_this.format(new Date(fields[n].value), "yyyyMMdd") : "";
          } else if (fields[n].ZZSHM == 'D' || fields[n].ZZSHM == 'Z' || fields[n].ZZSHM == 'B' || fields[n].ZZSHM == 'S') { //
            obj2[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? (fields[n].value.KEY == undefined ? "" : JSON.stringify(fields[n].value)) : "";
          } else if (fields[n].ZZSHM == 'T') { //
            var value = fields[n].value;
            if (value != undefined && value != "") {
              var values = value.split("-");
              obj2[fields[n].FIELD] = JSON.stringify({
                KEY: value.split("-")[0],
                VALUE: value.split("-")[values.length - 1]
              });
            } else {
              obj2[fields[n].FIELD] = JSON.stringify({
                KEY: "",
                VALUE: ""
              });
            }
          }else if(fields[n].ZKJLX == "PSR"){
            obj2[fields[n].FIELD] = (fields[n].value != undefined && fields[n].value != "") ? fields[n].value : "";
          } else {
            obj2[fields[n].FIELD] = fields[n].value == undefined ? "" : fields[n].value;
          }
        }
        iv_data.push(obj2);
        return iv_data;
      },
