      getData() {
        let t_this = this;
        var group = t_this.groupList;
        var iv_data = [];
        for (var i in group) {
          var sonGroup = group[i].ACBLK;
          for (var j in sonGroup) {
            var inftyList = sonGroup[j].INFTY;
            for (var k in inftyList) {
              if (inftyList[k].ZZTAB == 'X') {
                var models = inftyList[k].models;
                var objectData = {
                  INFTY: inftyList[k].INFTY,
                  SUBTY: inftyList[k].SUBTY,
                  VALUE: []
                };
                for (var o in models) {
                  var obj = {};
                  var fields = models[o];
                  for (var n in fields) {
                    if (fields[n].FTYPE == 'D') {   //
                      obj[fields[n].FIELD] = fields[n].value != "" && fields[n].value != undefined ? t_this.format(new Date(fields[n].value), "yyyyMMdd") : "";
                    } else if (fields[n].ZZSHM == 'D' || fields[n].ZZSHM == 'Z' || fields[n].ZZSHM == 'B' || fields[n].ZZSHM == 'S') { //
                      obj[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? (fields[n].value.KEY == undefined ? "" : JSON.stringify(fields[n].value)) : "";
                    } else if (fields[n].ZZSHM == 'T') { //
                      var value = fields[n].value;
                      if (value != undefined && value != "") {
                        var values = value.split("-");
                        obj[fields[n].FIELD] = JSON.stringify({
                          KEY: value.split("-")[0],
                          VALUE: value.split("-")[values.length - 1]
                        });
                      } else {
                        obj[fields[n].FIELD] = JSON.stringify({
                          KEY: "",
                          VALUE: ""
                        });
                      }
                    }else if(fields[n].ZKJLX == "PSR"){
                      obj[fields[n].FIELD] = (fields[n].value != undefined && fields[n].value != "") ? JSON.stringify(fields[n].value) : "";
                    } else {
                      obj[fields[n].FIELD] = fields[n].value == undefined ? "" : fields[n].value;
                    }
                  }
                  objectData.VALUE.push(obj);

                }
                objectData.VALUE = JSON.stringify(objectData.VALUE); //
                iv_data.push(objectData);
              } else {
                var objectData = {
                  INFTY: inftyList[k].INFTY,
                  SUBTY: inftyList[k].SUBTY,
                  VALUE: []
                };
                var obj = {};
                var fields = inftyList[k].FIELD;
                for (var n in fields) {
                  if (fields[n].FTYPE == 'D') {   //
                    obj[fields[n].FIELD] = fields[n].value != "" && fields[n].value != undefined ? t_this.format(new Date(fields[n].value), "yyyyMMdd") : "";
                  } else if (fields[n].ZZSHM == 'D' || fields[n].ZZSHM == 'Z' || fields[n].ZZSHM == 'B' || fields[n].ZZSHM == 'S') { //
                    obj[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? JSON.stringify(fields[n].value) : "";
                  } else if (fields[n].ZZSHM == 'T') { //
                    var value = fields[n].value;
                    if (value != undefined && value != "") {
                      var values = value.split("-");
                      obj[fields[n].FIELD] = JSON.stringify({
                        KEY: value.split("-")[0],
                        VALUE: value.split("-")[values.length - 1]
                      });
                    } else {
                      obj[fields[n].FIELD] = JSON.stringify({
                        KEY: "",
                        VALUE: ""
                      });
                    }

                  }else if(fields[n].ZKJLX == "PSR"){
                    obj[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? JSON.stringify(fields[n].value) : "";
                  } else {
                    obj[fields[n].FIELD] = fields[n].value == undefined ? "" : fields[n].value;
                  }
                }
                objectData.VALUE.push(obj);
                objectData.VALUE = JSON.stringify(objectData.VALUE); //
                iv_data.push(objectData);
              }


            }
          }
        }
        return iv_data;
      },
