 .titlelist {
   font-size: 20px;
  line-height: 60px;
 }
 
  .slide-fade-enter-active {
    transition: all .2s ease;
  }
  .slide-fade-leave-active {

    transition: all .2s ease;
  }
  .slide-fade-enter, .slide-fade-leave-to
  {
    left:0;right: 0;
    transform: translateX(50%);
    opacity: 0;
  }
  
  .info-content-b {
    margin-top: 56px;
    padding: 6px;
  }
  .info-title {
