  }
  .info-content {
    background:#fff;position:fixed;height:100%;width:100%;top:0px;bottom:0px;overflow: auto;
  }

  .main-content-title {
    font-size: 16px;
    border-left: 4px solid orange;
    padding-left: 5px;
    margin-top: 6px;
  }
  .main-field {
    font-size: 14px;
    line-height: 20px;
  }
</style>



